-txt-muted\" @click=cartChange> <p> <span class=attributes v-for=\"attributes in good.sku.skuAttributes\">{{attributes.key}}：{{attributes.value}}</span> <i class=\"ui-icon ui-icon-unfold cart-change-btn\"></i> </p> </div> </div> </div> <div class=ui-list-action> <div class=cart-c v-if=editorStatus> <h5 class=ui-list-price>¥{{good.sku.nowprice/100|currency ''}}</h5> <a class=y-servie-link></a> <p class=\"ui-nowrap ui-txt-muted\">×{{good.number}}</p> </div> <div class=cart-editor v-else> <p class=\"ui-nowrap cart-editor-del\" @click=\"delGoodEvent(good, result.cartItemGroup,-1)\"><i class=icon-del></i></p> </div> </div> </li> </ul> <footer class=ui-footer> <div class=ui-avatar-s> <p> <label class=ui-checkbox-s id=selectall> <input type=checkbox name=c-all v-model=selecteAllState @click=selecteAll> </label> </p> <label for=c-all>全选</label> </div> <div class=ui-list-info> <h2>合计：¥{{totalNowPrice/100|currency ''}}</h2> <h5>不含运费</h5> </div> <a class=\"btn-footer-default btn-b-red\" id=cart_calculate v-if=editorStatus v-link=\"{ name:'myorderConfirm'}\">结算</a> <a class=\"btn-footer-default btn-b-red\" id=cart_delall v-else @click=delGoodAllEvent(result.cartItemGroup)>删除</a> </footer> </div> </div> <confirm :confirm=confirm></confirm> <cart-add-module :cartadd=cartadd></cart-add-module> <toast :show.sync=toast.show :text.sync=toast.text :time=1000 :type.sync=toast.type></toast> </div> ";


