import Vue from 'vue'
import Router from 'vue-router'
import VueResource from 'vue-resource'
import VueValidate from 'vue-validator'
import App from './app.vue'
import {routes,redirections } from './config/routes'

// 引入组件
Vue.use(Router)
Vue.use(VueResource)
Vue.use(VueValidate)

Vue.config.debug = true

// Initialize router
const router = new Router({})
router.map(routes);
router.redirect(redirections);

Vue.http.options.root = 'static/mock';
// Vue.http.options.root = process.env.NODE_ENV === 'development' ? 'static/mock' : 'http://api.madian.com';
// Vue.http.options.root = 'http://api.madian.com'
Vue.http.options.emulateJSON = true
Vue.http.options.emulateHTTP = true


router.start(App,'app');



